$bg : #eee;
$right_w : 80% ;
ul,ol{
  list-style: none;
}
p{
  margin:0;
  line-height:100%;
}
a:hover{
  text-decoration : none;
}
#header{
  background:$bg;
  border-bottom:1px solid #ddd;
  text-align:center;
}
#main{
  min-height:700px;
}
#footer{
  background:$bg;
  border-top:1px solid #ddd;
  text-align:center;
  padding: 20px 0;
  clear:both;
}
.right-block{
  width:$right_w;
  float:right;
}
.list-prod{
  margin: 10px;
  display:block;
  font-size:0;
  li{
    display:inline-block;
    width:25%;
    vertical-align: top;
    font-size:13px;
    text-align:center;
    margin: 0 0 20px;
    position:relative;
    img{
      max-width:98%;
    }
    .tit-prod{
      font-size:20px;
      padding: 5px;
    }
    .time-prod{
      border-top : 1px dashed #ddd;
      padding: 4px ;
      margin : 0 5px;
    }
  }
}
// animate event
.listProd{
  position:relative;
   transition : all 0.5s ease-out;
  -webkit-transition : all 0.5s ease-out;
}
.listProd.ng-enter,.listProd.ng-move,.listProd.ng-leave.ng-leave-active {
  opacity:0;
  left: 300px
}
.listProd.ng-enter.ng-enter-active,.listProd.ng-move.ng-move-active,.listProd.ng-leave {
  opacity:1;
  left : 0
}